:root {
  --bg-color: #ddd;
  --text-color: #000;
  --primary-color: #4f90cd;
  --secondary-color: #7c7c7c;
}

body,
html {
  margin: 0;
}

body {
  background: var(--bg-color);
  color: var(--text-color);
  font: normal 13px arial, sans-serif;
}

h1,
a,
a:active,
a:visited,
a:hover {
  color: var(--primary-color);
  text-decoration: none;
}

img {
  max-width: 100%;
}

.container {
  margin: auto;
  max-width: 1024px;
  background: #fff;
  box-shadow: 0px 0px 15px --var(--bg-color);
}

.content {
  padding: 2rem;
  display: grid;
  grid-template-columns: 55% 40%;
  column-gap: 3rem;
}

.uppercase {
  text-transform: uppercase;
}

h1 {
  font-size: 1.6rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.4rem;
}

h4 {
  font-size: 1.3rem;
}

h5 {
  font-size: 1.2rem;
}

h6 {
  font-size: 1.1rem;
}

.text-primary {
  color: var(--primary-color);
}

.text-secondary {
  color: var(--secondary-color);
}
